const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    extendedIcon: {
        marginRight: theme.spacing.unit,
    },
   myNav: {
       backgroundColor:'white',
       display:'flex',
       justifyContent:'center',
    },
});

export default styles;


